import {getCapture} from '../../api/index.js';

let state = {
    isShowCity: false,
    isSendSMS: false,   //
    timer: 0,           //
    currentCity: '',
    city: {},    // 
}

let mutations = {
    showCity(state, payload){
        state.isShowCity = payload;
    },
    updateCity(state, payload){
        state.city = payload;
        state.isShowCity = false;
    },
    updateCapture(state, payload){
        if (payload.code == 0){
            state.isSendSMS = true;
            state.timer = 60;
            let inter = setInterval(()=>{
                state.timer--;
                if (state.timer == 0){
                    clearInterval(inter);
                    state.isSendSMS = false;
                }
            }, 1000);
        }else{
            alert(payload.msg);
        }
    }
}

let actions = {
    getCapture({commit}, payload){
        getCapture(payload)
        .then(body=>{
            console.log(body);
            commit('updateCapture', body);
        })
    }
}

export default{
    namespaced: true,
    state,
    actions,
    mutations
}



